import React from 'react'
import { Home, Search, Info, Settings } from '@material-ui/icons'
import Logo from '../Logo.png'
import '../sass/styles.css'

function Header() {
    return (
        <section className='header'>
            <ul>
                <li><img src={Logo} width='60px' height='60px'></img></li>
                <li><Home></Home></li>
                <li><Search></Search></li>
                <li><Info></Info></li>
                <li><Settings></Settings></li>
            </ul>
        </section>
    )
}

export default Header